
.mu-popover{position:fixed;background:#ffffff;border-radius:2px;max-height:100%;overflow:visible;-webkit-overflow-scrolling:touch;box-shadow:rgba(0,0,0,0.117647) 0 1px 6px,rgba(0,0,0,0.117647) 0 1px 4px;transform-origin:center top
}
.mu-popover-enter-active,.mu-popover-leave-active{transition-duration:300ms;transition-property:opacity,transform
}
.mu-popover-enter,.mu-popover-leave-active{transform:scaleY(0);opacity:0
}


