
.mu-focus-ripple-wrapper{height:100%;width:100%;position:absolute;top:0;left:0;overflow:hidden
}
.mu-focus-ripple{position:absolute;height:100%;width:100%;border-radius:50%;opacity:.16;background-color:currentColor;animation:mu-pulsate 750ms cubic-bezier(.445, .05, .55, .95);animation-iteration-count:infinite;animation-direction:alternate
}
@keyframes mu-pulsate{
0%{transform:scale(.72)
}
100%{transform:scale(.85)
}
}


