
.mu-tooltip{position:absolute;font-size:10px;line-height:22px;padding:0 8px;z-index:300;color:#ffffff;overflow:hidden;top:-1000px;border-radius:2px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;opacity:0;transition:top 0ms cubic-bezier(.23, 1, .32, 1) 450ms,transform 450ms cubic-bezier(.23, 1, .32, 1) 0ms,opacity 450ms cubic-bezier(.23, 1, .32, 1) 0ms
}
