
.mu-drawer{width:256px;position:fixed;top:0;bottom:0;overflow:auto;-webkit-overflow-scrolling:touch;transition-property:transform,visibility;transition-duration:.45s;transform:translate3d(-100%, 0, 0);border-radius:0;left:0;visibility:hidden;z-index:200
}
.mu-drawer::-webkit-scrollbar{display:none !important;width:0 !important;height:0 !important;-webkit-appearance:none;opacity:0 !important
}
.mu-drawer.right{right:0;left:auto;transform:translate3d(100%, 0, 0)
}
.mu-drawer.open{transform:translate3d(0, 0, 0);visibility:visible
}


