
.mu-circle-ripple{position:absolute;width:100%;height:100%;left:0;top:0;pointer-events:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;border-radius:50%;background-color:currentColor;background-clip:padding-box;opacity:.1
}
.mu-ripple-enter-active,.mu-ripple-leave-active{transition:opacity 2s cubic-bezier(.23, 1, .32, 1),transform 1s cubic-bezier(.23, 1, .32, 1)
}
.mu-ripple-enter{transform:scale(0)
}
.mu-ripple-leave-active{opacity:0 !important
}


